@import url(https://bootswatch.com/4/spacelab/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css2?family=Archivo+Black&family=Noto+Sans:wght@400;700&family=Roboto+Mono&display=swap);
.Error.alert{padding:0.5rem 0.75rem}.Error.alert .alert-heading{font-size:1rem}

.Keypad button:focus{box-shadow:0 0 0 0.1rem rgba(255,255,255,0.3)}.Screen.alert{padding:0.5rem;min-height:80px;margin-bottom:0.25rem;position:relative}.Screen.alert .buffer-amount{font-size:25px;line-height:25px;text-align:right}.Screen.alert .Transaction{position:absolute;top:-1px;left:-1px;right:-1px;padding:1px 5px;font-family:'Roboto Mono', monospace;font-size:smaller}.Screen.alert .Transaction+div{margin-top:15px}table.Keys{width:calc(100% + 6px);border-spacing:0;border-collapse:collapse;margin-left:-3px;margin-bottom:3px}table.Keys td{padding:3px}table.Keys thead button{width:100%;height:40px;background-image:none}table.Keys thead button:hover{background-image:none !important}table.Keys thead button:hover[disabled]{border-color:#333}table.Keys tbody td{width:25%}table.Keys tbody button{width:100%;height:62px;font-family:'Nato Sans', sans-serif;font-size:25px}table.Keys tbody td[rowspan='2'] button{height:130px}

.Payment.alert{padding:0.5rem}

.container{max-width:300px;font-size:0.8rem;padding:0.5rem;font-family:'Noto Sans', sans-serif}.form-group label{font-weight:bold}.form-group input{font-family:'Roboto Mono', monospace;font-size:0.72rem;padding:0.4rem}.list-group-item{padding:0.5rem 0.75rem}.card{background-color:#484944;color:#e6ebe7}.card-header{padding:0.5rem;padding-bottom:0;border-bottom:0;display:flex}.card-header .Logo path{fill:#5fa42d}.card-header h3{font-family:'Archivo Black', cursive;font-weight:bold;font-size:20px;color:rgba(0,0,0,0.8);opacity:0.5;text-shadow:0 1px 1px rgba(255,255,255,0.3),0 -1px 1px rgba(0,0,0,0.3);margin-bottom:0;line-height:30px;flex-grow:1;padding-left:0.5rem}.card-header .Power{margin-top:-3px;fill:#09070c}.card-body{padding:0.5rem;padding-bottom:0}.card-footer{padding:0.5rem;border-top:0}

